.App-screen {
    background-color: #0a043c;
    color: white;
    height: 100vh;
    font-family: 'Nunito', sans-serif;
}
.header-left-col , .header-middle-col , .header-right-col {
    display: flex;
    justify-content: center;
    background-color: #5F37D8;
    padding: 10px;
}
.questions-palette {
    position: relative;
    top: 10px;
    display: flex;
    justify-content: center;
}

.questionNoButton {
    margin-left: 30px;
    border: 1px solid black;
    border-radius: 50%;
    padding: .8%;
    width: 40px;
    display: flex;
    justify-content: center;
    background-color: white;
    color: black;
}
.navigation-buttons > div >button {
    background-color: #5F37D8;
    color: white;
    border: none;
   font-size: 20px;
   border-top-left-radius: 4px;
   border-top-right-radius: 4px;
}

.middle-section {
    height: 63%;
    margin-top: 1%;
}
.ques-num {
   position: relative;
   top: 18px;
   background-color: white;
   color: black;
   padding: 3%;
   border-top-right-radius: 4px;
   border-bottom-right-radius: 4px;
}
.question-statement {
    display: flex;
    justify-content: center;
    padding-top: 2%;
    font-size: 20px;
    padding-bottom: 1%;
}
.question-template {
    background-color: #252C4A;
    color: white;
    width: 80%;
    position: relative;
    left: 10.5%;
    border-radius: 5px;
    height: 100%;
    position: relative;
    top: 10%;
    padding-bottom: 40px;
}
.options {
    position: relative;
    left: 3.5%;
}
.options > li {
    list-style-type: none;
    padding: 1%;
    border: 2px solid #107EEB;
    margin-top: 2%;
    width: 50%;
    border-radius: 3px;
    font-size: 15px;
}
.question-template-bottom {
    font-size: 13px;
    position: relative;
    left: 10%;
}
.clear-response {
    background-color: #107EEB;
    color: white;
    position: relative;
    left: 35%;
    border: 2px;
    font-size: 12px;
    border-radius: 2px;
}
.greet-user {
    display: flex;
    justify-content: center;
    font-size: 20px;
    margin-top: 50px;
}
.start-quiz {
    display: flex;
    justify-content: center;
    height: 50%;
}
.start-quiz > button {
    margin-top: 7%;
    border: 2px solid #107EEB ;
    background:none;
    color: white;
    border-radius: 5px;
    font-size: 20px;
    width: 100px;
    height: 50px;
}
.footer {
    position: fixed;
    bottom: 0px;
    display: flex;
    justify-content: center;
    left: 30.5%;
}
.prev-btn {
    color: white;
}
.countDown {
    position: relative;
    top: 22px;
    background: white;
    color: #16c79a;
    padding: 5%;
    border-radius: 5px;
    font-weight: 900;
}
@media only screen and (min-width:1347px){
    .footer {
        position: fixed;
    bottom: 0px;
    display: flex;
    justify-content: center;
    left:33%;
    }
}
@media only screen and (max-width: 991px){

  .question-statement {
      margin-top: 10%;
  }
  .middle-screen {
      margin-top:20px;
  }
  .question-template {
      top: 20px;
  }
  .options > li {
      width: 150px;
      margin-left: 20px;
  }
  .options {
    top: 20px;
    margin-left: 20px;
}
.question-template-bottom {
    top: 20px;
}
.clear-response {
    top: 20px;
}
.footer {
    position: fixed;
    bottom: 0px;
     
    font-size: 10px;
    left:-19px;
}
}
@media only screen and (max-width: 400px){
    .greet-user {
        top: 10%;
    }
    .options {
        margin-left: 10px;
    }
    .question-template-bottom {
        top: 20px;
    }
    .clear-response {
        top: 20px;
    }
    .countDown {
        width: 10px;
    }
}